import { type NodeProps, type Node } from "@xyflow/react";
import { WebHookIcon } from "@/assets/NodeIcon";
import OutputHandle from "../react-flow/OutputHandle";
import NodeWrapper from "../react-flow/NodeWrapper";

export default function SocialData({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  return (
    <NodeWrapper>
      <div className="node__content node-outer outgrowData-box">
        <div className="title-box">
          <div className="node-head web__hook">
            <div className="flex-cnt">
              <WebHookIcon />
              <span className="no-height node__title">Webhook</span>
            </div>
            <div className="info-id">
              <div className="info-id-text ">ID:</div>
              <div id="webHook__node__id">{id}</div>
            </div>
          </div>
          <div className="box-head">
            <div className="node-wrap">
              <div className="node-body">
                <div className="node__elipses">Trigger Flow via Webhook</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <OutputHandle />
    </NodeWrapper>
  );
}
