import { useQuery } from "@tanstack/react-query";
import { axiosGet } from "@/services/axiosService";

export default function useLinkedinConfig() {
  return useQuery({
    queryKey: ["configurations", "linkedin"],
    queryFn: async () => {
      const response = await axiosGet("/configurations/linkedinConfigs");
      return response.data.data.configData;
    },
    staleTime: Infinity,
  });
}
