import { useNodeId } from "@xyflow/react";
import useStore from "../store";

export default function NodeError() {
  const nodeId = useNodeId();
  const nodeErrors = useStore((state) => state.nodeErrors);

  if (!nodeId || !nodeErrors[nodeId]) return null;

  return (
    <div className="node__error nodrag fx al-cnt gap-5">
      <svg
        width="12"
        height="12"
        viewBox="0 0 12 12"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          fillRule="evenodd"
          clipRule="evenodd"
          d="M12 6C12 7.5913 11.3679 9.11742 10.2426 10.2426C9.11742 11.3679 7.5913 12 6 12C4.4087 12 2.88258 11.3679 1.75736 10.2426C0.632141 9.11742 0 7.5913 0 6C0 4.4087 0.632141 2.88258 1.75736 1.75736C2.88258 0.632141 4.4087 0 6 0C7.5913 0 9.11742 0.632141 10.2426 1.75736C11.3679 2.88258 12 4.4087 12 6ZM6.75 9C6.75 9.19891 6.67098 9.38968 6.53033 9.53033C6.38968 9.67098 6.19891 9.75 6 9.75C5.80109 9.75 5.61032 9.67098 5.46967 9.53033C5.32902 9.38968 5.25 9.19891 5.25 9C5.25 8.80109 5.32902 8.61032 5.46967 8.46967C5.61032 8.32902 5.80109 8.25 6 8.25C6.19891 8.25 6.38968 8.32902 6.53033 8.46967C6.67098 8.61032 6.75 8.80109 6.75 9ZM6 2.25C5.80109 2.25 5.61032 2.32902 5.46967 2.46967C5.32902 2.61032 5.25 2.80109 5.25 3V6C5.25 6.19891 5.32902 6.38968 5.46967 6.53033C5.61032 6.67098 5.80109 6.75 6 6.75C6.19891 6.75 6.38968 6.67098 6.53033 6.53033C6.67098 6.38968 6.75 6.19891 6.75 6V3C6.75 2.80109 6.67098 2.61032 6.53033 2.46967C6.38968 2.32902 6.19891 2.25 6 2.25Z"
          fill="#FB5F66"
        />
      </svg>

      <ul>
        {nodeErrors[nodeId].map((error, index) => (
          <li key={index}>{error}</li>
        ))}
      </ul>
    </div>
  );
}
