import { useState, useMemo, useEffect, useRef } from "react";
import { decode, isBlurhashValid } from "blurhash";

export const ROW_WIDTH = 200;
export const ROW_HEIGHT = 10;
export const ROW_GAP = 10;

interface Pops {
  src: string;
  width: number;
  height: number;
  hash: string;
  onSelect: () => void;
}

export default function Image({ src, height, width, hash, onSelect }: Pops) {
  const canvas = useRef<HTMLCanvasElement | null>(null);
  const [isLoaded, setIsLoaded] = useState(false);

  const { imgWidth, imgHeight, gridRowEnd } = useMemo(
    () => getImageStyle(width, height),
    [width, height]
  );

  useEffect(() => {
    if (canvas.current) {
      const check = isBlurhashValid(hash);
      if (check.result) {
        const pixels = decode(hash, imgWidth, imgHeight);
        const ctx = canvas.current.getContext("2d");

        if (ctx) {
          const imageData = ctx.createImageData(imgWidth, imgHeight);
          imageData.data.set(pixels);
          ctx.putImageData(imageData, 0, 0);
        }
      }
    }
  }, [hash, imgWidth, imgHeight]);

  return (
    <>
      <canvas
        ref={canvas}
        className="image-search__canvas"
        width={imgWidth}
        height={imgHeight}
        style={{
          display: isLoaded ? "none" : "inline",
          gridRowEnd,
          height: imgHeight,
        }}
      />
      <img
        className="image-search__image"
        style={{
          display: isLoaded ? "inline" : "none",
          height: imgHeight,
          gridRowEnd,
        }}
        src={src}
        alt="image"
        data-height={height}
        data-width={width}
        onLoad={() => setIsLoaded(true)}
        onClick={onSelect}
      />
    </>
  );
}

function getImageStyle(width: number, height: number) {
  const scaleDownRatio = width / ROW_WIDTH;
  const scaleDownHeight = height / scaleDownRatio;

  const spanCount = Math.floor(
    (scaleDownHeight + ROW_GAP) / (ROW_HEIGHT + ROW_GAP)
  );

  return {
    imgWidth: ROW_WIDTH,
    imgHeight: (ROW_HEIGHT + ROW_GAP) * spanCount - ROW_GAP,
    gridRowEnd: "span " + spanCount,
    aspectRatio: width / height,
  };
}
