import { useState } from "react";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import _ from "lodash";
import SegmentDatePicker from "@/pages/omni-builder/components/SegmentDatePicker";
import { axiosPost } from "@/services/axiosService";
import { useNavigate } from "react-router-dom";

export default function CampaignModal({
  campaignData,
  setCampaignData,
  onSuccess,
  botUrl = "",
  onClose,
}) {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [showDatePicker, setShowDatePicker] = useState(false);
  const formatter = new Intl.DateTimeFormat("en-US", {
    dateStyle: "medium",
    timeStyle: "medium",
  });

  async function sendCampaign() {
    try {
      if (!campaignData.data.audienceData.listId) return;
      // const delay: number = moment(selectedAudience?.scheduleTime).diff(
      //   moment()
      // );
      await axiosPost("/campaign/trigger", {
        url: botUrl
          ? botUrl.split("/")[2]
          : window.location.pathname.split("/")[2],
        listId: campaignData.data.audienceData.listId,
        segmentId: campaignData.data.audienceData.segmentId,
        type: campaignData.data.audienceData.type,
        delay: 0,
      });

      navigate(botUrl);

      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "success",
          message: "Sent Campaign Successfully",
        }),
      );
      onSuccess();
    } catch (err: any) {
      console.log(err);
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: err.response?.data?.message || err?.message,
        }),
      );
    }
  }

  return (
    <div
      className="createbot-modal go-live-popup publish_modal"
      onClick={onClose}
    >
      <div
        className="publish_settings p-absolute fx fx-d-col al-cnt"
        onClick={(e) => e.stopPropagation()}
      >
        <div className="publish_head fx al-cnt fx-d-col w-100">
          <h2>Send Campaign</h2>
          {/* <p className="mt-8">Send Campaign Freely</p> */}
          <div className="close new-modal-close flex-cnt" onClick={onClose}>
            <svg
              width="10"
              height="10"
              viewBox="0 0 10 10"
              fill="#6D6D6D"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                fill="#6D6D6D"
              />
            </svg>
          </div>
          <div className="inputs-data-box audience-rght">
            <div className="condition"></div>
            <div className="audience_campaign grid gap-20">
              <div className="data-box-wrap">
                <div className="send_campaign">
                <label htmlFor="now" className="assign-logic fx al-cnt">
                  <input
                    id="now"
                    value="now"
                    type="radio"
                    name="sendCampaign"
                    checked={
                      campaignData?.data?.audienceData?.scheduleType === "now"
                    }
                    onChange={() => setCampaignData({ scheduleType: "now" })}
                  />
                 Send campaign now</label>
                </div>
              </div>
              <div className="data-box-wrap">
                <div className="send_campaign">
                <label htmlFor="delay"  className="assign-logic fx al-cnt ">
                  <input
                    id="delay"
                    type="radio"
                    value="delay"
                    name="sendCampaign"
                    checked={
                      campaignData?.data?.audienceData?.scheduleType === "delay"
                    }
                    onChange={() => setCampaignData({ scheduleType: "delay" })}
                  />
                  Send campaign Later</label>
                </div>
              </div>
            </div>
          </div>
          {campaignData?.data?.audienceData?.scheduleType === "delay" && (
            <div className="inputs-data-box w-100">
              <span className="data-box-label mg-top mb-0">Schedule campaign</span>
              <div className="data-box-wrap pl-0">
                <div className="input-label">Select date and time</div>
                <div
                  className="segment-date-input p-relative"
                  onClick={() => setShowDatePicker(!showDatePicker)}
                >
                  <svg
                    width="14"
                    height="14"
                    viewBox="0 0 14 14"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M11.9855 12.6H2.00405V4.9H11.9855V12.6ZM9.84664 0V1.4H4.14294V0H2.71701V1.4H2.00405C1.21266 1.4 0.578125 2.023 0.578125 2.8V12.6C0.578125 12.9713 0.728356 13.3274 0.995769 13.5899C1.26318 13.8525 1.62587 14 2.00405 14H11.9855C12.3637 14 12.7264 13.8525 12.9938 13.5899C13.2612 13.3274 13.4115 12.9713 13.4115 12.6V2.8C13.4115 2.4287 13.2612 2.0726 12.9938 1.81005C12.7264 1.5475 12.3637 1.4 11.9855 1.4H11.2726V0H9.84664ZM10.5596 7.7H6.99479V11.2H10.5596V7.7Z"
                      fill="#fb5f66"
                    />
                  </svg>
                  <span className="analytics-date-range">
                    {formatter.format(Date.now())}
                  </span>
                  {showDatePicker && <SegmentDatePicker />}
                </div>
              </div>
            </div>
          )}
        </div>

        <div className="btm-sticky__btn flex-cnt">
          <button
            className="saveDataBtn no-mt"
            onClick={sendCampaign}
            disabled={!campaignData.data.audienceData.listId}
          >
            Send Campaign
          </button>
        </div>
      </div>
    </div>
  );
}
