import type { Node } from "@xyflow/react";
import AskQuestion from "../nodes/AskQuestion";
import Button from "../nodes/Button";
import Email from "../nodes/Email";
import Phone from "../nodes/Phone";
import Username from "../nodes/Username";
import Address from "../nodes/Address";
import Result from "../nodes/Result";
import SendMessage from "../nodes/SendMessage";
import SocialData from "../nodes/SocialData";
import Segment from "../nodes/Segment";
import LiveChat from "../nodes/LiveChat";
import SendEmail from "../nodes/SendEmail";
import SendSms from "../nodes/SendSms";
import HitApi from "../nodes/HitApi";
import DelayAction from "../nodes/DelayAction";
import PushNotification from "../nodes/PushNotification";
import AbTest from "../nodes/AbTest";
import Condition from "../nodes/Condition";
import Assignees from "../nodes/Assignees";
import WhatsApp from "../nodes/WhatsApp";
import Envoie from "../nodes/Envoie";
import OpenAi from "../nodes/OpenAi";
import LinkedIn from "../nodes/LinkedIn";
import DynamicApps from "../nodes/DynamicApps";
import Sticky from "../nodes/Sticky";
import Campaign from "../nodes/Campaign";

import Edge from "./Edge";

export const nodeTypes = {
  campaign: Campaign,
  ask_question: AskQuestion,
  button: Button,
  email: Email,
  phone: Phone,
  username: Username,
  address: Address,
  result: Result,
  sendMessage: SendMessage,
  socialData: SocialData,
  segment: Segment,
  liveChat: LiveChat,
  sendEmail: SendEmail,
  sendSMS: SendSms,
  hitApi: HitApi,
  delayAction: DelayAction,
  pushNotification: PushNotification,
  abTest: AbTest,
  condition: Condition,
  assignees: Assignees,
  whatsApp: WhatsApp,
  envoie: Envoie,
  openai: OpenAi,
  linkedIn: LinkedIn,
  dynamicApps: DynamicApps,
  sticky: Sticky,
};

export const edgeTypes = {
  custom: Edge,
};

export const connectionLineStyle: React.CSSProperties = {
  strokeWidth: 2,
  stroke: "#2b8de9",
  strokeDasharray: 10,
  WebkitAnimation: "dashdraw 10s linear infinite",
  animation: "dashdraw 10s linear infinite",
};

export function nodeColor(node: Node): string {
  switch (node.type) {
    case "ask_question":
      return "#7479F21A";
    case "button":
      return "#F371711A";
    case "email":
      return "#BB3DDB1A";
    case "phone":
      return "#19B21F0D";
    case "username":
      return "#99620F0D";
    case "address":
      return "#5640D60D";
    case "result":
      return "#923FD20D";
    case "sendMessage":
      return "#2989CF0D";
    case "socialData":
      return "#C73A630D";
    case "segment":
      return "#B023910D";
    case "sendEmail":
      return "#BB3DDB0D";
    case "sendSMS":
      return "#29A2B20D";
    case "hitApi":
      return "#C547470D";
    case "delayAction":
      return "#85A6000D";
    case "pushNotification":
      return "#DC3C2F0D";
    case "abTest":
      return "#00A1E20D";
    case "condition":
      return "#4A64A80D";
    case "assignees":
      return "#8C33D20D";
    case "whatsApp":
      return "#26B53C1A";
    case "whatsAppTemplate":
      return "#26B53C1A";
    case "envoie":
      return "#6865A5";
    case "openai":
      return "#0000000D";
    case "linkedIn":
      return "#29A2B20D";
    case "dynamicApps":
      return "#6865A5";
    case "sticky":
      return "#D9880D0D";
    default:
      return "#e2e2e2";
  }
}
