import { type NodeProps, type Node } from "@xyflow/react";
import { ABTestingIcon } from "@/assets/NodeIcon";
import InputHandle from "../react-flow/InputHandle";
import OutputHandle from "../react-flow/OutputHandle";
import NodeWrapper from "../react-flow/NodeWrapper";

export default function AbTest({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  return (
    <NodeWrapper>
      <div className="node__content node-outer ab-node-outer">
        <div className="title-box">
          <div className="node-head ab__test">
            <div className="flex-cnt">
              <ABTestingIcon />
              <span className="no-height node__title">A/B test</span>
            </div>
            <div className="info-id">
              <div className="info-id-text ">ID:</div>
              <div id="ab__test__id">{id}</div>
            </div>
          </div>
          <div className="box-head">
            <div className="ab-test-wrap">
              <div className="ab-test-value-wrap">
                <span id="ab-test-value_a">A: {data.abTestData.percent}%</span>
                <span id="ab-test-value_b">
                  B: {100 - data.abTestData.percent}%
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <InputHandle />
      <OutputHandle id="output_1" style={{ left: 105 }} />
      <OutputHandle id="output_2" style={{ left: 165 }} />
    </NodeWrapper>
  );
}
