import { useMemo } from "react";
import { type NodeProps, type Node } from "@xyflow/react";
import {
  AskQuestionIcon,
  GoogleSheetIcon,
  HubspotIcon,
  OpenAIIcon,
} from "@/assets/NodeIcon";
import InputHandle from "../react-flow/InputHandle";
import OutputHandle from "../react-flow/OutputHandle";
import NodeWrapper from "../react-flow/NodeWrapper";

export default function DynamicApps({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  const appData = useMemo(() => APPS_DATA[data.nodeName], [data.nodeName]);

  if (!appData) return null;

  return (
    <NodeWrapper>
      <div
        className="node__content node-outer open_ai_box"
        style={{ borderColor: appData.borderColor }}
      >
        <div className="title-box">
          <div className="node-head" style={{ background: appData.background }}>
            <div className="flex-cnt">
              {appData.icon}
              <span className="no-height node__title">{appData.name}</span>
            </div>
            <div className="info-id">
              <div className="info-id-text ">ID:</div>
              <div>{id}</div>
            </div>
          </div>
          <div className="box-head">
            <div className="node-wrap">
              <div className="node-body">Start building your flow</div>
            </div>
          </div>
        </div>
      </div>
      <InputHandle />
      <OutputHandle />
    </NodeWrapper>
  );
}

const APPS_DATA = {
  hubspot: {
    name: "Hubspot",
    background: "#FF7A590D",
    icon: <HubspotIcon />,
    borderColor: "#FF7A59",
  },
  openai: {
    name: "Open AI",
    background: "#0000000D",
    icon: <OpenAIIcon />,
    borderColor: "#000000",
  },
  googleSheet: {
    name: "Google Sheets",
    background: "#23A5660D",
    icon: <GoogleSheetIcon />,
    borderColor: "#23A566",
  },
  linkedIn: {
    name: "Linkedin",
    background: "#23A5660D",
    icon: <AskQuestionIcon />,
    borderColor: "#23A566",
  },
};
