import { type NodeProps, type Node } from "@xyflow/react";
import { NotificationIcon } from "@/assets/NodeIcon";
import InputHandle from "../react-flow/InputHandle";
import OutputHandle from "../react-flow/OutputHandle";
import NodeWrapper from "../react-flow/NodeWrapper";

export default function PushNotification({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  return (
    <NodeWrapper>
      <div className="node__content node-outer push-notification-box">
        <div className="title-box">
          <div className="node-head send__notification">
            <div className="flex-cnt">
              <NotificationIcon />
              <span className="no-height node__title">Send Notification</span>
            </div>
            <div className="info-id">
              <div className="info-id-text ">ID:</div>
              <div id="notification__node__id">{id}</div>
            </div>
          </div>
          <div className="box-head">
            <div className="node-wrap">
              <div className="node-body">Send Notification</div>
            </div>
          </div>
        </div>
      </div>
      <InputHandle />
      <OutputHandle />
    </NodeWrapper>
  );
}
