import { useNodeId } from "@xyflow/react";
import { useDroppable } from "@dnd-kit/core";
import FirstNodeHandle from "../components/FirstNodeHandle";
import NodeToolbar from "../components/NodeToolbar";
import NodeError from "../components/NodeError";

interface Props {
  children: React.ReactNode;
  disableFirstNode?: boolean;
  disableToolbar?: boolean;
}

export default function NodeWrapper({
  children,
  disableFirstNode,
  disableToolbar,
}: Props) {
  const nodeId = useNodeId();

  const { setNodeRef } = useDroppable({
    id: `node/${nodeId}`,
    data: { nodeId },
  });

  return (
    <div className="node__wrapper" ref={setNodeRef}>
      {children}
      {!disableFirstNode && <FirstNodeHandle />}
      {!disableToolbar && <NodeToolbar />}
      <NodeError />
    </div>
  );
}
