import { useInfiniteQuery } from "@tanstack/react-query";
import axios from "axios";

export default function useSearchUnlash(query: string) {
  async function searchUnplash({ pageParam }) {
    const response = await axios.get("https://api.unsplash.com/search/photos", {
      params: {
        client_id: "VQqCoMy8pswiI6dC7Q7NDm-pFIY3r9N3Cxt1LMqvhRo",
        query: query || "random",
        page: pageParam,
        per_page: 20,
      },
    });

    return response.data.results;
  }

  return useInfiniteQuery({
    queryKey: ["unsplash", "search", { query }],
    initialPageParam: 1,
    getNextPageParam: (lastPage, _, lastPageParam) =>
      lastPage.length ? lastPageParam + 1 : undefined,
    queryFn: searchUnplash,
    staleTime: 10 * 1000,
  });
}
