import { type NodeProps, type Node } from "@xyflow/react";
import { AudienceIcon } from "@/assets/NodeIcon";
import OutputHandle from "../react-flow/OutputHandle";
import NodeWrapper from "../react-flow/NodeWrapper";

export default function Segment({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  return (
    <NodeWrapper>
      <div className="node__content node-outer segment-node-outer">
        <div className="title-box">
          <div className="node-head audience__head">
            <div className="flex-cnt">
              <AudienceIcon />
              <span className="no-height node__title">Audience</span>
            </div>
            <div className="info-id">
              <div className="info-id-text ">ID:</div>
              <div id="segment__id">{id}</div>
            </div>
          </div>
          <div className="box-head">
            <div className="node-wrap">
              <div className="node-body segment-select-inner">
                {data.audienceData.name}
              </div>
            </div>
          </div>
        </div>
      </div>
      <OutputHandle />
    </NodeWrapper>
  );
}
