import { type NodeProps, type Node } from "@xyflow/react";
import { DelayActionIcon } from "@/assets/NodeIcon";
import InputHandle from "../react-flow/InputHandle";
import OutputHandle from "../react-flow/OutputHandle";
import NodeWrapper from "../react-flow/NodeWrapper";

export default function DelayAction({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  return (
    <NodeWrapper>
      <div className="node__content node-outer delay-action-box">
        <div className="title-box">
          <div className="node-head delay__action">
            <div className="flex-cnt">
              <DelayActionIcon />
              <span className="no-height node__title">Delay Action</span>
            </div>
            <div className="info-id">
              <div className="info-id-text ">ID:</div>
              <div id="delay__node__id">{id}</div>
            </div>
          </div>
          <div className="box-head">
            <div className="node-wrap">
              <div className="node-body">
                <div className="fx jstfy-sp-btw">
                  <div>{data.delayAction.time}</div>
                  <div className="capitalized">
                    {data.delayAction.timeFormat}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <InputHandle />
      <OutputHandle />
    </NodeWrapper>
  );
}
