import useStore from "../store";
import useAutoSave from "../AutoSaveContext";

export default function FirstNode() {
  const autoSave = useAutoSave();
  const selectedNodeId = useStore((state) => state.selectedNodeId);
  const startNodeId = useStore((state) => state.metaData.startNodeId);
  const setMetaData = useStore((state) => state.setMetaData);

  const isFirstNode = selectedNodeId === startNodeId;

  function handleFirstNodeSet() {
    setMetaData({ startNodeId: isFirstNode ? "" : selectedNodeId });
    autoSave();
  }

  return (
    <button
      className={`first-node-Btn flex-cnt gap-5 ${
        isFirstNode ? "selected" : ""
      }`}
      onClick={handleFirstNodeSet}
    >
      <svg
        width="28"
        height="28"
        viewBox="0 0 28 28"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <rect width="28" height="28" rx="14" fill="#2B8DE9" />
        <g clipPath="url(#clip0_2488_57972)">
          <path
            d="M21.8983 13.7537L19.8113 11.6668C19.7119 11.5674 19.5616 11.5381 19.4323 11.5917C19.3022 11.6453 19.2173 11.7726 19.2173 11.9131V12.9565H15.0435V8.78273H16.0869C16.2274 8.78273 16.3547 8.69785 16.4083 8.56779C16.4619 8.43771 16.4319 8.28813 16.3332 8.18865L14.2463 6.10174C14.1106 5.96609 13.8901 5.96609 13.7544 6.10174L11.6675 8.18865C11.568 8.28881 11.5381 8.43768 11.5917 8.56779C11.6453 8.69785 11.7726 8.78273 11.9131 8.78273H12.9565V12.9565H8.78273V11.9131C8.78273 11.7726 8.69785 11.6453 8.56777 11.5917C8.43979 11.5375 8.28881 11.5674 8.18864 11.6668L6.10174 13.7537C5.96609 13.8894 5.96609 14.1099 6.10174 14.2456L8.18864 16.3325C8.28881 16.4327 8.43908 16.4633 8.56777 16.4076C8.69785 16.3548 8.78273 16.2275 8.78273 16.0869V15.0435H12.9565V19.2173H11.9131C11.7726 19.2173 11.6453 19.3022 11.5917 19.4323C11.5381 19.5623 11.5681 19.7119 11.6668 19.8113L13.7537 21.8983C13.8219 21.9657 13.9109 21.9999 14 21.9999C14.0891 21.9999 14.1781 21.9657 14.2463 21.8983L16.3332 19.8113C16.4327 19.7119 16.4626 19.5624 16.4083 19.4323C16.354 19.3022 16.2274 19.2173 16.0869 19.2173H15.0435V15.0435H19.2173V16.0869C19.2173 16.2274 19.3022 16.3547 19.4323 16.4083C19.5609 16.4632 19.7119 16.4327 19.8113 16.3332L21.8983 14.2463C22.0339 14.1099 22.0339 13.8901 21.8983 13.7537Z"
            fill="white"
          />
        </g>
        <defs>
          <clipPath id="clip0_2488_57972">
            <rect
              width="16"
              height="16"
              fill="white"
              transform="translate(6 6)"
            />
          </clipPath>
        </defs>
      </svg>
      <span>
        {isFirstNode ? "Selected as First Step" : "Select as First Step"}
      </span>
    </button>
  );
}
