import {
  BaseEdge,
  EdgeLabelRenderer,
  getSmoothStepPath,
  useReactFlow,
  MarkerType,
  type EdgeProps,
} from "@xyflow/react";
import useStore from "../store";
import useAutoSave from "../AutoSaveContext";
import clsx from "clsx";

export default function Edge({
  id,
  sourceX,
  sourceY,
  targetX,
  targetY,
  sourcePosition,
  targetPosition,
}: EdgeProps) {
  const autoSave = useAutoSave();
  const reactFlow = useReactFlow();
  const removeEdge = useStore((state) => state.removeEdge);
  const setVariables = useStore((state) => state.setVariables);
  const edgeHoverId = useStore((state) => state.edgeHoverId);
  const isHovered = edgeHoverId === id;

  const [edgePath, labelX, labelY] = getSmoothStepPath({
    sourceX,
    sourceY,
    sourcePosition,
    targetX,
    targetY,
    targetPosition,
  });

  function handleEdgeDelete() {
    removeEdge(id);
    autoSave();
    updateVariables();
  }

  function updateVariables() {
    const selectedNodeId = useStore.getState().selectedNodeId;

    if (!selectedNodeId) return;
    const node = reactFlow.getNode(selectedNodeId);
    if (!node) return;
    setVariables(node);
  }

  return (
    <>
      <BaseEdge
        id={id}
        path={edgePath}
        style={{ strokeWidth: 2 }}
        markerEnd={MarkerType.ArrowClosed}
        className={clsx(isHovered && "hover")}
      />
      <EdgeLabelRenderer>
        {isHovered && (
          <button
            className="edge__label nodrag nopan flex-cnt"
            style={{
              transform: `translate(-50%, -50%) translate(${labelX}px,${labelY}px)`,
              pointerEvents: "all",
            }}
            onClick={handleEdgeDelete}
          >
            <DeleteIcon />
          </button>
        )}
      </EdgeLabelRenderer>
    </>
  );
}

function DeleteIcon() {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width="14"
      height="14"
      viewBox="0 0 14 14"
      fill="none"
    >
      <path
        d="M12.2513 2.33332H10.443C10.1639 0.976254 8.9701 0.00175 7.58462 0H6.41794C5.03246 0.00175 3.83869 0.976254 3.55962 2.33332H1.75129C1.42913 2.33332 1.16797 2.59448 1.16797 2.91665C1.16797 3.23881 1.42913 3.5 1.75129 3.5H2.33462V11.0833C2.33656 12.6934 3.64127 13.9981 5.25129 14H8.75129C10.3613 13.9981 11.666 12.6934 11.668 11.0833V3.5H12.2513C12.5735 3.5 12.8346 3.23884 12.8346 2.91668C12.8346 2.59451 12.5735 2.33332 12.2513 2.33332ZM6.41797 9.91668C6.41797 10.2388 6.15681 10.5 5.83464 10.5C5.51245 10.5 5.25129 10.2388 5.25129 9.91668V6.41668C5.25129 6.09451 5.51245 5.83335 5.83462 5.83335C6.15678 5.83335 6.41794 6.09451 6.41794 6.41668V9.91668H6.41797ZM8.75129 9.91668C8.75129 10.2388 8.49013 10.5 8.16797 10.5C7.8458 10.5 7.58465 10.2388 7.58465 9.91668V6.41668C7.58465 6.09451 7.8458 5.83335 8.16797 5.83335C8.49013 5.83335 8.75129 6.09451 8.75129 6.41668V9.91668ZM4.76772 2.33332C5.01584 1.63466 5.67657 1.16755 6.41797 1.16665H7.58465C8.32604 1.16755 8.98678 1.63466 9.23489 2.33332H4.76772Z"
        fill="#FB5F66"
      />
    </svg>
  );
}
