import { useMemo } from "react";
import { Handle, Position, useNodeId, type EdgeProps } from "@xyflow/react";
import useStore from "../store";

export default function OutputHandle(props: Partial<EdgeProps>) {
  const nodeId = useNodeId();
  const edges = useStore((state) => state.edges);

  const isConnectable = useMemo(() => {
    if (!nodeId) return false;
    const outGoingEdges = edges.filter(
      (edge) =>
        edge.source === nodeId &&
        edge.sourceHandle === (props.id ?? "output_1"),
    );
    return !outGoingEdges.length;
  }, [edges]);

  return (
    <Handle
      className="node__handle"
      type="source"
      id="output_1"
      position={Position.Bottom}
      isConnectable={isConnectable}
      {...props}
    />
  );
}
