import type { Node, Edge, Connection } from "@xyflow/react";
import getEdgeId from "./getEdgeId";

export function dbToReactFlow(botData: Record<string, any>) {
  const { questions, ...metaData } = botData;

  const nodes: Array<Node<Record<string, any>>> = [];
  const edges: Array<Edge> = [];
  let maxNodeId = 0;

  for (const question of questions) {
    nodes.push({
      id: question.drawFlowId,
      type: question.name,
      position: {
        x: question.pos_x,
        y: question.pos_y,
      },
      data: question,
    });

    maxNodeId = Math.max(maxNodeId, +question.drawFlowId);

    for (const output of question.outputs) {
      if (!output.connections) continue;

      const connection: Connection = {
        source: question.drawFlowId,
        target: output.connections.node,
        sourceHandle: output.connectionId,
        targetHandle: output.connections.output,
      };
      const id = getEdgeId(connection);

      edges.push({
        ...connection,
        id,
        type: "custom",
        updatable: "target",
      });
    }
  }

  return {
    nodes,
    edges,
    metaData,
    nextNodeId: maxNodeId + 1,
  };
}

type ConnectionData = Record<
  string,
  Pick<Connection, "target" | "targetHandle">
>;

export function reactFlowToDb(
  nodes: Array<Node>,
  edges: Array<Edge>,
  metaData: Record<string, any> = {},
): Record<string, any> {
  const questionData: Array<Record<string, any>> = [];
  const connectionData: ConnectionData = {};

  for (const edge of edges) {
    if (!edge.sourceHandle) continue;
    connectionData[`${edge.source}__${edge.sourceHandle}`] = {
      target: edge.target,
      targetHandle: edge.targetHandle ?? "input_1",
    };
  }

  for (const node of nodes) {
    const outputs = generateNodeOutput(node, connectionData);

    questionData.push({
      ...node.data,
      drawFlowId: node.id,
      name: node.type,
      pos_x: node.position.x,
      pos_y: node.position.y,
      outputs,
    });
  }

  return {
    ...metaData,
    questions: questionData,
  };
}

function generateNodeOutput(
  node: Node<Record<string, any>>,
  connectionData: ConnectionData,
) {
  switch (node.type) {
    case "button": {
      return node.data.buttonDetails.buttons.map((button) => {
        const connection = connectionData[`${node.id}__${button.outputId}`];

        return {
          connectionId: button.outputId,
          ...(connection && {
            connections: {
              node: connection.target,
              output: connection.targetHandle,
            },
          }),
        };
      });
    }
    case "hitApi":
    case "abTest":
    case "condition": {
      const connection1 = connectionData[`${node.id}__output_1`];
      const connection2 = connectionData[`${node.id}__output_2`];

      return [
        {
          connectionId: "output_1",
          ...(connection1 && {
            connections: {
              node: connection1.target,
              output: connection1.targetHandle,
            },
          }),
        },
        {
          connectionId: "output_2",
          ...(connection2 && {
            connections: {
              node: connection2.target,
              output: connection2.targetHandle,
            },
          }),
        },
      ];
    }
    default: {
      const connection = connectionData[`${node.id}__output_1`];

      return [
        {
          connectionId: "output_1",
          ...(connection && {
            connections: {
              node: connection.target,
              output: connection.targetHandle,
            },
          }),
        },
      ];
    }
  }
}
