import { useState } from "react";
import { type NodeProps, type Node } from "@xyflow/react";
import { CampaignIcon } from "@/assets/NodeIcon";
import OutputHandle from "../react-flow/OutputHandle";
import useStore from "../store";
import useAutoSave from "../AutoSaveContext";
import DropDownArrow from "@/assets/DropDownArrow";
import NodeWrapper from "../react-flow/NodeWrapper";

export default function Campaign({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  const autoSave = useAutoSave();
  const metaData = useStore((state) => state.metaData);
  const setMetaData = useStore((state) => state.setMetaData);
  const [showDropDown, setShowDropDown] = useState(false);

  function setCampaignType(campaignType: string) {
    setMetaData({ campaignType });
    autoSave();
  }

  return (
    <NodeWrapper disableFirstNode={true} disableToolbar={true}>
      <span className="campaign-start">Start</span>
      <div className="node__content node-outer campaign-box">
        <div className="title-box">
          <div className="node-head __campaign ">
            <div className="flex-cnt">
              <CampaignIcon />
              <span className="no-height node__title">Campaigns</span>
            </div>
            <div className="info-id">
              <div className="info-id-text ">ID:</div>
              <div id="webHook__node__id">{id}</div>
            </div>
          </div>
          <div className="box-head" onMouseLeave={() => setShowDropDown(false)}>
            <div className="node-wrap">
              <div className="node-body campaign no-p">
                <div
                  className="node-dropdown campaign"
                  onClick={(event) => {
                    event.stopPropagation();
                    setShowDropDown(!showDropDown);
                  }}
                >
                  {metaData.campaignType === "socialData"
                    ? "Webhook"
                    : "Audience"}
                  <DropDownArrow />
                  {showDropDown && (
                    <ol className="dropdown-items campaign">
                      <li onClick={() => setCampaignType("segment")}>
                        Audience
                      </li>
                      <li onClick={() => setCampaignType("socialData")}>
                        Webhook
                      </li>
                    </ol>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <OutputHandle />
    </NodeWrapper>
  );
}
