import { type NodeProps, type Node } from "@xyflow/react";
import { GetAddressIcon } from "@/assets/NodeIcon";
import InputHandle from "../react-flow/InputHandle";
import OutputHandle from "../react-flow/OutputHandle";
import NodeWrapper from "../react-flow/NodeWrapper";

export default function Address({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  return (
    <NodeWrapper>
      <div className="node__content node-outer adress-node-box">
        <div className="title-box">
          <div className="node-head get__address">
            <div className="flex-cnt">
              <GetAddressIcon />
              <span className="no-height node__title">Get Address</span>
            </div>
            <div className="info-id">
              <div className="info-id-text ">ID:</div>
              <div id="getAddress__node__id">{id}</div>
            </div>
          </div>
          <div className="box-head">
            <div className="node-wrap">
              <div className="node-body">
                <div className="node__elipses">{data.data.title}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <InputHandle />
      <OutputHandle />
    </NodeWrapper>
  );
}
