import { type NodeProps, type Node } from "@xyflow/react";
import { WhatsappIcon } from "@/assets/NodeIcon";
import InputHandle from "../react-flow/InputHandle";
import OutputHandle from "../react-flow/OutputHandle";
import NodeWrapper from "../react-flow/NodeWrapper";
import { Fragment, useEffect, useState } from "react";
import useWhatsAppConfig from "../react-query/configs/useWhatsAppConfig";
import axios from "axios";
import { NoImgIcon, TemplateArrow } from "../components/NewTemplate";
import environment from "@/constants/Environment";
import PdfToImageConverter from "../components/PdfToImage";

export default function WhatsApp({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  const environmentData = environment();
  const [selectedTemplate, setSelectedTemplate] = useState<any>();
  const [media, setMedia] = useState<any>();
  const whatsappConfigData = useWhatsAppConfig();

  const getTemplates = async (
    phoneNumbers: any[],
    wabaIds: string[],
    accessToken: string,
  ) => {
    await phoneNumbers.map(async (phone, index) => {
      if (phone.display_phone_number === data.whatsappDetails?.from) {
        const response = await axios.get(
          `https://graph.facebook.com/${environmentData.META_API_VERSION}/${wabaIds[index]}/message_templates?status`,
          {
            headers: {
              Authorization: `Bearer ${accessToken}`,
            },
          },
        );
        setTemplateData(response.data.data);
      }
    });
  };

  useEffect(() => {
    if (!whatsappConfigData.isLoading && whatsappConfigData.data) {
      const { phoneNumbers, wabaIds, accessToken } = whatsappConfigData.data;
      getTemplates(phoneNumbers, wabaIds, accessToken);
    }
  }, [whatsappConfigData.data, data?.whatsappDetails]);

  const setTemplateData = (templates) => {
    setMedia(
      data.whatsappDetails?.media?.length ? data.whatsappDetails?.media[0] : {},
    );
    const template = templates?.find(
      (item) => item.name === data?.whatsappDetails.templateName,
    );
    setSelectedTemplate(template);
  };

  return (
    <NodeWrapper>
      <div className="node__content node-outer whatsapp-box">
        <div className="title-box">
          <div className="box-head">
            <div className="node-wrap">
              <div className="whatsapp-head">
                <div className="fx al-cnt jstfy-sp-btw">
                  <WhatsappIcon />
                  <span className="no-height node__title">WhatsApp</span>
                </div>
                <div className="info-id">
                  <div className="info-id-text ">ID:</div>
                  <div>{id}</div>
                </div>
              </div>
              <div className="node-body mt-40">
                Send Whatsapp messages to user
              </div>
              <h3 className="prev_head mt-15">PREVIEW</h3>
              <div
                className={`template-preview-container pd-10 mt-5 ${
                  !selectedTemplate ? "flex-cnt pd-20 node-whtsp-prev" : ""
                }`}
              >
                {!selectedTemplate ? (
                  <div className="no-template-preview w-100 flex-cnt template-preview-wrapper br-10 pd-10 text-center">
                    No preview available
                  </div>
                ) : (
                  <div className="template-preview-wrapper pd-10 br-10 fx fx-d-col gap-20">
                    {selectedTemplate?.components.map((template, index) => (
                      <Fragment key={index}>
                        {template.format === "TEXT" && (
                          <h3 className="fw-500 no-m">{template.text}</h3>
                        )}
                        {template?.format &&
                          template?.format != "TEXT" &&
                          template?.type === "HEADER" &&
                          media[template.format.toLowerCase()] && (
                            <div className="flex-cnt data-box-wrap br-5 ovr-flw-hd template-preview-img no-p">
                              {template.format === "IMAGE" && (
                                <img
                                  src={media?.image}
                                  alt={media?.imageName}
                                />
                              )}
                              {template.format === "VIDEO" && (
                                <video src={media?.video} />
                              )}
                              {template.format === "DOCUMENT" && (
                                // <iframe
                                //   title="PDF Document"
                                //   src={media?.document}
                                // />
                                <PdfToImageConverter pdfUrl={media?.document} />
                              )}
                            </div>
                          )}
                        {template?.format &&
                          template?.format != "TEXT" &&
                          !media[template.format.toLowerCase()] && (
                            <div className="flex-cnt data-box-wrap br-5 ovr-flw-hd pd-25">
                              <NoImgIcon />
                            </div>
                          )}
                        {template.type === "BODY" && <p>{template.text}</p>}
                        {template.type === "FOOTER" && (
                          <p className="opacity-50">{template.text}</p>
                        )}
                        {template.type === "BUTTONS" && (
                          <div className="fx fx-d-col gap-10">
                            {template.buttons.map((item, index) => (
                              <span
                                className="flex-cnt template-cta gap-10"
                                key={index}
                              >
                                <TemplateArrow />
                                {item.text}
                              </span>
                            ))}
                          </div>
                        )}
                      </Fragment>
                    ))}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
      <InputHandle />
      <OutputHandle />
    </NodeWrapper>
  );
}
