import { type NodeProps, type Node } from "@xyflow/react";
import { AssigneesIcon } from "@/assets/NodeIcon";
import InputHandle from "../react-flow/InputHandle";
import OutputHandle from "../react-flow/OutputHandle";
import NodeWrapper from "../react-flow/NodeWrapper";

export default function Assignees({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  return (
    <NodeWrapper>
      <div className="node__content node-outer assignees-node-outer">
        <div className="title-box">
          <div className="node-head assign__head">
            <div className="fx al-cnt">
              <AssigneesIcon />
              <span className="no-height node__title">Assignees</span>
            </div>
            <div className="info-id">
              <div className="info-id-text ">ID:</div>
              <div id="assignees__id">{id}</div>
            </div>
          </div>
          <div className="box-head">
            <div className="node-wrap">
              <div className="node-body">
                <div className="node__elipses">Assignees</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <InputHandle />
      <OutputHandle />
    </NodeWrapper>
  );
}
