import {
  useReactFlow,
  useUpdateNodeInternals,
  type NodeProps,
  type Node,
} from "@xyflow/react";
import { produce } from "immer";
import { SingleSelectIcon } from "@/assets/NodeIcon";
import type { Image } from "../types";
import InputHandle from "../react-flow/InputHandle";
import OutputHandle from "../react-flow/OutputHandle";
import useAutoSave from "../AutoSaveContext";
import NodeWrapper from "../react-flow/NodeWrapper";
import { ButtonLimits } from "../constant";

interface Button {
  name: string;
  value: string;
  outputId: string;
  image?: Image;
}

export default function Button({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  const { updateNodeData } = useReactFlow();
  const updateNodeInternals = useUpdateNodeInternals();
  const autoSave = useAutoSave();

  function handleButtonAdd(event: React.MouseEvent<HTMLButtonElement>) {
    event.stopPropagation();
    if (data.buttonDetails.buttons.length >= ButtonLimits.MAX_BUTTON) return;

    const maxOutputId = Number(
      data.buttonDetails.buttons.at(-1)?.outputId.split("_").at(-1) ?? 0,
    );

    const updatedData = produce(data, (draft: any) => {
      draft.buttonDetails.buttons.push({
        name: "option",
        value: 0,
        outputId: `output_${maxOutputId + 1}`,
      });
    });

    updateNodeData(id, updatedData);
    updateNodeInternals(id);
    autoSave();
  }

  function handleButtonRemove(
    event: React.MouseEvent<HTMLButtonElement>,
    buttonIndex: number,
  ) {
    event.stopPropagation();
    if (data.buttonDetails.buttons.length === 1) return;

    const updatedData = produce(data, (draft: any) => {
      draft.buttonDetails.buttons.splice(buttonIndex, 1);
    });

    updateNodeData(id, updatedData);
    updateNodeInternals(id);
    autoSave();
  }

  return (
    <NodeWrapper>
      <div className="node__content node-outer btn-box">
        <div className="title-box">
          <div className="node-head single__select">
            <div className="flex-cnt">
              <SingleSelectIcon />
              <span className="no-height node__title">Single Select</span>
            </div>
            <div className="info-id">
              <div className="info-id-text ">ID:</div>
              <div id="btn__node__id">{id}</div>
            </div>
          </div>
          <div className="box-head">
            <div className="node-wrap">
              <div className="button-content">
                <div className="outputs newButton btn-num-0 p-relative">
                  <div className="df-btn-head flex">
                    <div
                      className="btn-mb button-input text-wrap"
                      id="btn-question"
                      df-title=""
                    >
                      <div className="node__elipses">{data.data.title}</div>
                    </div>
                  </div>
                </div>
                <hr />
                <div
                  className={`box fx-d-row fx-wrap gap-10 ${
                    !data.buttonDetails.showImg ? "button-no-img" : ""
                  } ${
                    data.buttonDetails.buttons.length === 1 ? "one-button" : ""
                  }`}
                >
                  {data.buttonDetails.buttons.map(
                    (button: Button, buttonIndex: number) =>
                      button.outputId !== "output_0" && (
                        <ButtonRow
                          key={button.outputId}
                          data={button}
                          onRowRemove={(event) =>
                            handleButtonRemove(event, buttonIndex)
                          }
                        />
                      ),
                  )}
                </div>
                <hr />
                <button
                  className="add-buttons add_new_button cp"
                  style={{ justifyContent: "center" }}
                  onClick={handleButtonAdd}
                >
                  <AddIcon /> Add New Button
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <InputHandle />
      {data.buttonDetails.buttons.map((button: Button, index: number) => (
        <OutputHandle
          key={button.outputId}
          id={button.outputId}
          style={{ left: index * 118 + 70 }}
        />
      ))}
    </NodeWrapper>
  );
}

function ButtonRow({
  data,
  onRowRemove,
}: {
  data: Button;
  onRowRemove: (event: React.MouseEvent<HTMLButtonElement>) => void;
}) {
  return (
    <div className="inputs outputs newButton p-relative fx-d-col flex-cnt">
      <div className="button-image w-100 no-m">
      <img
          src={
            data.image?.url ||
            "https://dlvkyia8i4zmz.cloudfront.net/RDDE3m5IS0qHQZZXsM8w_Group_26815.png"
          }
          alt="Button Image"
          className="w-100"
        />
       
      </div>
      <div id={data.outputId} className="button-input ">
        <div className="node__elipses__1">{data.name}</div>
      </div>
      <button className="delete-button no-m mt-10" onClick={onRowRemove}>
        <DeleteIcon />
      </button>
    </div>
  );
}

