import { type NodeProps, type Node } from "@xyflow/react";
import { GetUsernameIcon } from "@/assets/NodeIcon";
import InputHandle from "../react-flow/InputHandle";
import OutputHandle from "../react-flow/OutputHandle";
import NodeWrapper from "../react-flow/NodeWrapper";

export default function UserName({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  return (
    <NodeWrapper>
      <div className="node__content node-outer user-name-box">
        <div className="title-box">
          <div className="node-head get__username">
            <div className="fx al-cnt jstfy-sp-btw">
              <GetUsernameIcon />
              <span className="no-height node__title">Get Username</span>
            </div>
            <div className="info-id">
              <div className="info-id-text ">ID:</div>
              <div id="getUser__node__id">{id}</div>
            </div>
          </div>
          <div className="box-head">
            <div className="node-wrap">
              <div className="node-body">{data.data.title}</div>
            </div>
          </div>
        </div>
      </div>
      <InputHandle />
      <OutputHandle />
    </NodeWrapper>
  );
}
