import { type NodeProps, type Node } from "@xyflow/react";
import { ConditionIcon } from "@/assets/NodeIcon";
import InputHandle from "../react-flow/InputHandle";
import OutputHandle from "../react-flow/OutputHandle";
import NodeWrapper from "../react-flow/NodeWrapper";

interface ConditionData {
  variable: string;
  value: string;
  logical_operator: string;
  comparison_operator: string;
}

export default function Condition({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  return (
    <NodeWrapper>
      <div className="node__content node-outer condition-node-outer">
        <div className="title-box">
          <div className="node-head condition__node">
            <div className="fx al-cnt">
              <ConditionIcon />
              <span className="no-height node__title">Condition</span>
            </div>
            <div className="info-id">
              <div className="info-id-text ">ID:</div>
              <div id="condition__id">{id}</div>
            </div>
          </div>
          <div className="box-head">
            <div className="condition-wrap">
              <div className="condition-body">
                {data.conditionData.map(
                  (condition: ConditionData, index: number) => (
                    <div key={index} className="condition-div fx-d-col no-gap">
                      <div className="fx gap-6">
                        <span>{index ? condition.logical_operator : "If"}</span>
                        <span className="condition-variable">
                          {condition.variable
                            ? "@" + condition.variable
                            : "___"}
                        </span>
                      </div>
                      <div className="fx gap-6">
                        <span>{condition.comparison_operator}</span>
                        <span className="condition-variable">
                          {condition.value
                            ? condition.value.length > 8
                              ? condition.value.slice(0, 8) + "..."
                              : condition.value
                            : "___"}
                        </span>
                      </div>
                    </div>
                  )
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
      <InputHandle />
      <OutputHandle id="output_1" style={{ left: 105 }} />
      <OutputHandle id="output_2" style={{ left: 165, borderColor: "red" }} />
    </NodeWrapper>
  );
}
