import { type NodeProps, type Node } from "@xyflow/react";
import NodeWrapper from "../react-flow/NodeWrapper";

export default function Sticky({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  return (
    <NodeWrapper disableFirstNode={true}>
      <div className="builder-info-tooltip node-outer">
        <div className="info-tooltip-wrapper">
          <div className="info-tooltip-head">
            <div className="info-pin-svg">
              <PinIcon />
            </div>
          </div>
          <div className="info-tooltip-body">
            <div
              className="content"
              dangerouslySetInnerHTML={{
                __html: data.stickyDetails.content,
              }}
            />
            {/* <div className="content">{data.stickyDetails.content}</div> */}
          </div>
        </div>
      </div>
    </NodeWrapper>
  );
}

function PinIcon() {
  return (
    <svg
      width={14}
      height={14}
      viewBox="0 0 14 14"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M8.04309 0.446761C8.57789 -0.08804 9.46599 -0.121013 10.0466 0.446761L13.5526 3.95283C14.1059 4.50607 14.1059 5.40306 13.5526 5.9563C13.0178 6.4911 12.1298 6.53038 11.5492 5.9563L11.2988 5.70574L9.20811 7.79639C9.83881 9.50334 9.41655 11.6098 8.04309 12.9684C7.89483 13.1151 7.66664 13.096 7.54222 12.9684L4.53688 9.96302L2.28312 12.2171C2.14481 12.3555 1.92056 12.3555 1.78225 12.2171C1.65458 12.0895 1.63214 11.8676 1.78225 11.7163L4.03609 9.46222L1.03095 6.45717C0.903278 6.3295 0.878709 6.10854 1.03095 5.9563C2.371 4.61625 4.4064 4.13663 6.20265 4.79093L8.2933 2.70028L8.04309 2.45023C7.47223 1.87643 7.50829 0.981563 8.04309 0.446761Z"
        fill="#4F4F4F"
      />
    </svg>
  );
}
