import { type NodeProps, type Node } from "@xyflow/react";
import { CallApiIcon } from "@/assets/NodeIcon";
import InputHandle from "../react-flow/InputHandle";
import OutputHandle from "../react-flow/OutputHandle";
import NodeWrapper from "../react-flow/NodeWrapper";

export default function HitApi({
  id,
  data,
}: NodeProps<Node<Record<string, any>>>) {
  return (
    <NodeWrapper>
      <div className="node-outer call-api-box">
        <div className="title-box">
          <div className="node-head call__api">
            <div className="flex-cnt">
              <CallApiIcon />
              <span className="no-height node__title">Call API</span>
            </div>
            <div className="info-id">
              <div className="info-id-text ">ID: </div>
              <div id="callApi__node__id">{id}</div>
            </div>
          </div>
          <div className="box-head">
            <div className="node-wrap">
              <div className="node-body">Call an API</div>
            </div>
          </div>
        </div>
      </div>
      <InputHandle />
      <OutputHandle id="output_1" style={{ left: 105 }} />
      <OutputHandle id="output_2" style={{ left: 165 }} />
    </NodeWrapper>
  );
}
