import environment from "@/constants/Environment";
import { init as FileStackClint, type PickerOptions } from "filestack-js";

export default function uploadImage(options?: Partial<PickerOptions>) {
  const defaultOptions: PickerOptions = {
    accept: ["image/*", "video/*", "application/pdf"],
    fromSources: ["local_file_system", "url"],
    transformations: {
      crop: false,
      circle: true,
      rotate: true,
    },
    maxFiles: 1,
    minFiles: 1,
    maxSize: 10 * 1024 * 1024,
    ...options,
  };

  const client = FileStackClint(environment().FILESTACK_API_KEY);
  const picker = client.picker(defaultOptions);
  picker.open();
}
