import { useMemo } from "react";
import { useReactFlow, useNodeId } from "@xyflow/react";
import useStore from "../store";
import useAutoSave from "../AutoSaveContext";
import NODE_INFO from "@/constants/NodeInfo";

export default function NodeToolbar() {
  const nodeId = useNodeId();
  const autoSave = useAutoSave();
  const duplicateNode = useStore((state) => state.duplicateNode);
  const removeNode = useStore((state) => state.removeNode);

  function handleNodeRemove(event: React.MouseEvent) {
    event.stopPropagation();
    if (!nodeId) return;

    removeNode(nodeId);
    autoSave();
  }

  function handleNodeDuplicate(event: React.MouseEvent) {
    event.stopPropagation();
    if (!nodeId) return;

    duplicateNode(nodeId);
    autoSave();
  }

  return (
    <div className="node__toolbar toolbar">
      <div className="toolbar__buttons">
        <div className="node-info">
          <InfoIcon />
          <Info />
        </div>
        <div className="copy-icon" onClick={handleNodeDuplicate}>
          <CopyIcon />
        </div>
        <div className="cross-icon" onClick={handleNodeRemove}>
          <DeleteIcon />
        </div>
      </div>
    </div>
  );
}

function Info() {
  const reactFlow = useReactFlow();
  const nodeId = useNodeId();
  const nodeType = useMemo(
    () => nodeId && reactFlow.getNode(nodeId)?.type,
    [nodeId]
  );

  const { name, description } = NODE_INFO[nodeType ?? "unknown"];

  return (
    <div className="info-tooltip">
      <div className="info-data">
        <div className="info-row">
          <div className="info-name">
            <span className="info-name-text">NAME: </span>
            <span>{name}</span>
          </div>
          <div className="info-id">
            <span className="info-id-text">ID:</span>
            <span>{nodeId || "NA"}</span>
          </div>
        </div>
        <div className="info-row">
          <div className="info-left">
            <span className="info-text">INFO: </span>
          </div>
          <div className="info-right">
            <span>{description}</span>
          </div>
        </div>
      </div>
    </div>
  );
}

function InfoIcon() {
  return (
    <svg
      width="26"
      height="26"
      viewBox="0 0 26 26"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <circle cx="13" cy="13" r="12.5" fill="#E8F5FF" stroke="#3E9ADC" />
      <path
        d="M13 8.24902C12.4486 8.24902 12 8.69792 12 9.24969C12 9.80096 12.4486 10.2495 13 10.2495C13.5514 10.2495 14 9.80096 14 9.24969C14 8.69792 13.5514 8.24902 13 8.24902Z"
        fill="#3395DD"
      />
      <path
        d="M13.0002 11.75C12.5859 11.75 12.25 12.0859 12.25 12.5002V17.0012C12.25 17.4155 12.5859 17.7513 13.0002 17.7513C13.4145 17.7513 13.7503 17.4155 13.7503 17.0012V12.5002C13.7503 12.0859 13.4145 11.75 13.0002 11.75Z"
        fill="#3395DD"
      />
    </svg>
  );
}

