import { useState, Fragment, useEffect } from "react";
import { motion } from "framer-motion";
import SearchIcon from "@/assets/SearchIcon";
import Image, { ROW_WIDTH, ROW_HEIGHT, ROW_GAP } from "./Image.tsx";
import type { Image as ImageI } from "../types.ts";
import uploadImage from "@/services/fileStack.service";
import useSearchUnlash from "@/hooks/useSearchUnplash";
import { PickerOptions } from "filestack-js";

export default function ImageSearchPanel({ onSelect, onClose, type }) {
  const [searchText, setSearchText] = useState("");
  const [query, setQuery] = useState("");
  const [fileStackLoader, setFileStackLoader] = useState(false);

  const { data, status, isFetchingNextPage, fetchNextPage } =
    useSearchUnlash(query);

  async function handleImageUpload() {
    setFileStackLoader(true);
    uploadImage({
      accept: ["image/jpeg", "image/png", "image/jpg"],
      onFileUploadFinished: (data) => {
        onSelect({
          url: data.url,
          name: data.filename,
          size: data.size,
        });
        onClose();
      },
      onOpen: () => {
        setFileStackLoader(false);
      },
    });
  }

  const getImageSize = async (url: string) => {
    try {
      const response = await fetch(url);
      const blob = await response.blob();
      const imageSize = blob?.size;
      return imageSize;
    } catch (error) {
      console.error("Error fetching image size:", error);
    }
  };

  useEffect(() => {
    const element = document.querySelector(".builder") as HTMLDivElement;
    if (element) element.style.zIndex = "100";
    return () => {
      if (element) element.style.zIndex = "98";
    };
  }, []);

  return type === "sidebar" ? (
    <div className="data-box-container upload-image-container">
      <div className="fx al-cnt mb-20">
        <div className="fx al-cnt gap-10 goBack-btn no-m" onClick={onClose}>
          <BackArrowIcon />
        </div>
        <div className="fx al-cnt gap-10">
          <div className="node-search no-m no-p">
            <SearchIcon />
            <input
              placeholder="Search"
              value={searchText}
              onChange={(event) => setSearchText(event.target.value)}
              onKeyDown={(event) =>
                event.key === "Enter" && setQuery(searchText)
              }
            />
          </div>
          <div
            className="fx al-cnt gap-10 uploadImg-btn cp"
            onClick={handleImageUpload}
          >
            {
              <img src="https://dlvkyia8i4zmz.cloudfront.net/xi16S0pSZOBAe6L94Q16_Upload_icon.svg" />
            }
            {fileStackLoader ? "Loading.." : "UPLOAD IMAGE"}
          </div>
        </div>
      </div>
      <div className="image-search">
        {status === "pending" && (
          <div className="skeleton">
            <div className="skeleton-images">
              <div className="skeleton-img skeleton-img1"></div>
              <div className="skeleton-img skeleton-img2"></div>
              <div className="skeleton-img skeleton-img3"></div>
              <div className="skeleton-img skeleton-img4"></div>
            </div>
            <div className="skeleton-images">
              <div className="skeleton-img skeleton-img5"></div>
              <div className="skeleton-img skeleton-img6"></div>
              <div className="skeleton-img skeleton-img7"></div>
            </div>
          </div>
        )}
        {status === "error" && (
          <div className="no-invoice-found">
            <NoDataFound />
            <h1>Error!!</h1>
            <p>Something went wrong</p>
          </div>
        )}
        {status === "success" &&
          (data.pages.some((page) => page.length) ? (
            <>
              <div
                className="image-search__gallery grey-scrollbar"
                style={{
                  gridTemplateColumns: `repeat(auto-fit, minmax(${ROW_WIDTH}px, 1fr))`,
                  gridAutoRows: ROW_HEIGHT,
                  gap: ROW_GAP,
                }}
              >
                {data.pages.map((page, pageIndex) => (
                  <Fragment key={pageIndex}>
                    {page.map((data, index) => (
                      <Image
                        key={index}
                        src={data.urls.regular}
                        width={data.width}
                        height={data.height}
                        hash={data.blur_hash}
                        onSelect={() => {
                          onSelect({
                            url: data.urls.regular,
                            name: data.alt_description,
                            size: 0,
                          });
                          onClose();
                        }}
                      />
                    ))}
                  </Fragment>
                ))}
              </div>
              <motion.div
                className="image-search__load-more"
                initial={{ top: 0 }}
                whileInView={{ top: -30 }}
                onClick={() => !isFetchingNextPage && fetchNextPage()}
              >
                <button>
                  {isFetchingNextPage ? "Loading.." : "Load more"}
                </button>
              </motion.div>
            </>
          ) : (
            <div className="no-invoice-found">
              <NoDataFound />
              <h1>Nothing!!</h1>
              <p>No Data Found</p>
            </div>
          ))}
      </div>
    </div>
  ) : (
    <div
      className="createbot-modal createbot-new-background"
      onClick={(e) => {
        onClose();
        e.stopPropagation();
      }}
    >
      <div
        className="search-img-container"
        onClick={(e) => e.stopPropagation()}
      >
        <div className="search-img-header">
          <h2 className="txt-cnt">Upload image</h2>
        </div>

        <Fragment>
          <div className="upload-img-wrapper">
            <div className="fx jstfy-sp-btw">
              <div className="image-searchbox search-image width-60 no-m">
                <svg
                  width="16"
                  height="16"
                  viewBox="0 0 16 16"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M7.33594 13.333C10.6496 13.333 13.3359 10.6467 13.3359 7.33301C13.3359 4.0193 10.6496 1.33301 7.33594 1.33301C4.02223 1.33301 1.33594 4.0193 1.33594 7.33301C1.33594 10.6467 4.02223 13.333 7.33594 13.333Z"
                    stroke="#444444"
                    strokeWidth="1.5"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                  <path
                    opacity="0.4"
                    d="M15.002 14.9997L13.3359 13.333"
                    stroke="#444444"
                    strokeWidth="1.5"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                </svg>
                <input
                  placeholder="Search"
                  value={searchText}
                  className="rightSidebar-tarea rightSidebar-search no-mt"
                  onChange={(event) => setSearchText(event.target.value)}
                  onKeyDown={(event) =>
                    event.key === "Enter" && setQuery(searchText)
                  }
                />
              </div>
              <div
                className="fx al-cnt gap-10 uploadImg-btn cp mr-20"
                onClick={handleImageUpload}
              >
                {
                  <img src="https://dlvkyia8i4zmz.cloudfront.net/xi16S0pSZOBAe6L94Q16_Upload_icon.svg" />
                }
                {fileStackLoader ? "Loading.." : "UPLOAD IMAGE"}
              </div>
            </div>
            <div
              className={`image-container new-scrollbar ${
                status === "pending" && "skeleton-flow"
              }`}
            >
              <ul
                className={` no-mt ${
                  status === "pending" && "skeleton-visible"
                }`}
              >
                {status === "error" && (
                  <li>
                    <p className="noDataFound txt-lft">No Data Found...</p>
                  </li>
                )}
                {status === "pending" && (
                  <div className="skeleton">
                    <div className="skeleton-images">
                      <div className="skeleton-img skeleton-img1"></div>
                      <div className="skeleton-img skeleton-img2"></div>
                      <div className="skeleton-img skeleton-img3"></div>
                      <div className="skeleton-img skeleton-img4"></div>
                    </div>
                    <div className="skeleton-images">
                      <div className="skeleton-img skeleton-img5"></div>
                      <div className="skeleton-img skeleton-img6"></div>
                      <div className="skeleton-img skeleton-img7"></div>
                    </div>
                  </div>
                )}
                {status === "success" &&
                  data?.pages.map((page, pageIndex) => (
                    <Fragment key={pageIndex}>
                      {page.map((data, index) => (
                        <Image
                          key={index}
                          src={data.urls.regular}
                          width={data.width}
                          height={data.height}
                          hash={data.blur_hash}
                          onSelect={async () => {
                            const imageSize =
                              (await getImageSize(data.urls.regular)) ?? 0;
                            console.log(imageSize);
                            onSelect({
                              url: data.urls.regular,
                              name: data.alt_description,
                              size: imageSize,
                            });
                            onClose();
                          }}
                        />
                      ))}
                    </Fragment>
                  ))}
              </ul>
            </div>
          </div>
          {status === "success" && (
            <div className="upload-img-footer flex-cnt">
              <div
                className="load_more_btn load_more_button"
                onClick={() => !isFetchingNextPage && fetchNextPage()}
              >
                {isFetchingNextPage ? "Loading.." : "Load more"}
              </div>
            </div>
          )}
        </Fragment>
      </div>
    </div>
  );
}

function BackArrowIcon() {
  return (
    <svg
      width="20"
      height="20"
      viewBox="0 0 20 20"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M7.9724 4.94141L2.91406 9.99974L7.9724 15.0581"
        stroke="#444444"
        strokeWidth="1.5"
        strokeMiterlimit="10"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
      <path
        opacity="0.4"
        d="M17.0797 10H3.05469"
        stroke="#444444"
        strokeWidth="1.5"
        strokeMiterlimit="10"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </svg>
  );
}

