import  { useEffect, useState } from "react";

interface Month {
  name: string;
  days: number;
}

const SegmentDatePicker = () => {
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [selectedYear, setSelectedYear] = useState<number | null>(null);
  const [selectedMonth, setSelectedMonth] = useState(new Date().getMonth());
  const [selectedHour, setSelectedHour] = useState(2);
  const [selectedMinute, setSelectedMinute] = useState(17);
  const [selectedAmPm, setSelectedAmPm] = useState("PM");
  const [clickedDay, setClickedDay] = useState<number | null>(null);

  useEffect(() => {
    setSelectedDate(new Date());
  }, []);

  const handleHourSelect = (hour) => {
    setSelectedHour(hour);
  };

  const handleMinuteSelect = (minute) => {
    setSelectedMinute(minute);
  };

  const handleMonthClick = (monthIndex) => {
    setSelectedMonth(monthIndex);
  };

  const handleDateClick = (day: number) => {
    setClickedDay(day);
  };

  const generateDays = (
    numDays: number,
    startingDay: number,
  ): JSX.Element[] => {
    const days: JSX.Element[] = [];
    let currentDay = 1;
    let dayOfWeek = startingDay;

    while (currentDay <= numDays) {
      const rowDays: JSX.Element[] = [];
      for (let i = 0; i < 7; i++) {
        if (i < dayOfWeek || currentDay > numDays) {
          rowDays.push(<span key={`empty-${i}`}  style={{ pointerEvents: 'none' }}>&nbsp;</span>);
        } else {
          const day = currentDay;
          rowDays.push(
            <span key={currentDay} onClick={() => handleDateClick(day)}>
              {currentDay}
            </span>,
          );
          currentDay++;
        }
      }
      days.push(
        <li className="date-row fx" key={currentDay}>
          {rowDays}
        </li>,
      );
      dayOfWeek = 0;
    }

    return days;
  };

  const getStartingDayOfMonth = (date: Date): number => {
    const firstOfMonth = new Date(date.getFullYear(), date.getMonth(), 1);
    return firstOfMonth.getDay();
  };

  const currentMonth = selectedMonth;
  const currentYear = selectedDate.getFullYear();
  const isLeapYear =
    (currentYear % 4 === 0 && currentYear % 100 !== 0) ||
    currentYear % 400 === 0;
  const daysInFeb = isLeapYear ? 29 : 28;
  MONTHS[1].days = daysInFeb;
  const daysInMonth = MONTHS[currentMonth].days;
  const startingDayOfMonth = getStartingDayOfMonth(
    new Date(currentYear, currentMonth, 1),
  );
  const currentDay = selectedDate.getDate();

  return (
    <div className="segment-calendar" onClick={(e) => e.stopPropagation()}>
      <div className="year-month p-relative">
        <ul className="year-list">
          {Array.from({ length: 100 }, (_, index) => (
            <li className="mb-10" key={currentYear + index}>
              <span
                className={`year ${
                  selectedYear === currentYear + index ? "selected_year" : ""
                }`}
                onClick={() => setSelectedYear(currentYear + index)}
              >
                {currentYear + index}
              </span>
              <div
                className={`month-box ${
                  selectedYear === currentYear + index ? "open" : ""
                }`}
              >
                {MONTHS.map((month, monthIndex) => (
                  <div
                    key={month.name}
                    className="month"
                    onClick={() => handleMonthClick(monthIndex)}
                  >
                    {month.name}
                  </div>
                ))}
              </div>
            </li>
          ))}
        </ul>
      </div>
      <div className="date-wraper p-relative">
        <h4>
          {MONTHS[selectedMonth]?.name}{" "}
          {clickedDay !== null ? clickedDay : selectedDate.getDate()},{" "}
          {selectedYear || currentYear}
        </h4>
        <div className="days">
          <div className="week-row fx mt-10">
            {WEEKDAYS.map((day) => (
              <div key={day} className="weekday">
                {day}
              </div>
            ))}
          </div>
          <ul className="dates fx fx-d-col">
            {generateDays(daysInMonth, startingDayOfMonth)}
          </ul>
        </div>
      </div>
      <div className="time">
        <h4>
          {selectedHour.toString().padStart(2, "0")}:
          {selectedMinute.toString().padStart(2, "0")} {selectedAmPm}
        </h4>
        <div className="time-units fx jstfy-even">
          <div className="hours">
            <p className="time-heading">Hr</p>
            <ul>
              {Array.from({ length: 12 }, (_, index) => (
                <li
                  key={index}
                  onClick={() => handleHourSelect(index === 0 ? 12 : index)}
                  className={
                    selectedHour === (index === 0 ? 12 : index)
                      ? "active_time"
                      : ""
                  }
                >
                  {index === 0 ? 12 : index}
                </li>
              ))}
            </ul>
          </div>
          <div className="min">
            <p className="time-heading">Min</p>
            <ul>
              {Array.from({ length: 60 }, (_, index) => (
                <li
                  key={index}
                  onClick={() => handleMinuteSelect(index)}
                  className={selectedMinute === index ? "active_time" : ""}
                >
                  {index.toString().padStart(2, "0")}
                </li>
              ))}
            </ul>
          </div>
          <div className="am-pm">
            <p className="time-heading">AM/PM</p>
            <ul>
              {amPmOptions.map((option) => (
                <li
                  key={option.value}
                  onClick={() => setSelectedAmPm(option.value)}
                  className={selectedAmPm === option.value ? "active_time" : ""}
                >
                  {option.label}
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SegmentDatePicker;

const MONTHS: Month[] = [
  { name: "Jan", days: 31 },
  { name: "Feb", days: 28 },
  { name: "Mar", days: 31 },
  { name: "Apr", days: 30 },
  { name: "May", days: 31 },
  { name: "Jun", days: 30 },
  { name: "Jul", days: 31 },
  { name: "Aug", days: 31 },
  { name: "Sep", days: 30 },
  { name: "Oct", days: 31 },
  { name: "Nov", days: 30 },
  { name: "Dec", days: 31 },
];

const WEEKDAYS = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];

const amPmOptions = [
  { value: "AM", label: "AM" },
  { value: "PM", label: "PM" },
];
