import { useMemo } from "react";
import { useReactFlow, useNodeId } from "@xyflow/react";
import { motion } from "framer-motion";
import { useDraggable } from "@dnd-kit/core";
import useStore from "../store";

export default function FirstNodeHandle() {
  const reactFlow = useReactFlow();
  const nodeId = useNodeId();
  const startNodeId = useStore((state) => state.metaData.startNodeId);
  const { attributes, listeners, setNodeRef, transform } = useDraggable({
    id: `node/first__${nodeId}`,
  });

  const transformStr = useMemo(() => {
    const viewport = reactFlow.getViewport();
    const translateX = (transform?.x ?? 0) / viewport.zoom;
    const translateY = (transform?.y ?? 0) / viewport.zoom;

    return `translate(${translateX}px, ${translateY}px)`;
  }, [transform]);

  return (
    <motion.div
      className="first-node-handle nodrag"
      ref={setNodeRef}
      style={{
        transform: transformStr,
      }}
      initial={{ left: "-22px" }}
      whileHover={{ left: "-90px" }}
      {...attributes}
      {...listeners}
    >
      {nodeId === startNodeId && (
        <>
          <svg
            width="12"
            height="12"
            viewBox="0 0 12 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M11.9237 5.81531L10.3585 4.25013C10.2839 4.17552 10.1712 4.1536 10.0742 4.19379C9.97662 4.23397 9.91296 4.32944 9.91296 4.43482V5.21741H6.78259V2.08705H7.56519C7.67057 2.08705 7.76604 2.02339 7.80622 1.92584C7.8464 1.82828 7.82395 1.7161 7.74988 1.64149L6.18469 0.0763027C6.08296 -0.0254342 5.91756 -0.0254342 5.81583 0.0763027L4.25064 1.64149C4.17603 1.71661 4.1536 1.82826 4.19378 1.92584C4.23396 2.02339 4.32944 2.08705 4.43482 2.08705H5.21741V5.21741H2.08705V4.43482C2.08705 4.32944 2.02339 4.23397 1.92583 4.19379C1.82984 4.15309 1.71661 4.17552 1.64148 4.25013L0.0763026 5.81531C-0.0254342 5.91705 -0.0254342 6.08245 0.0763026 6.18418L1.64148 7.74937C1.71661 7.8245 1.82931 7.84746 1.92583 7.80571C2.02339 7.76607 2.08705 7.67059 2.08705 7.56519V6.7826H5.21741V9.91296H4.43482C4.32944 9.91296 4.23396 9.97662 4.19378 10.0742C4.1536 10.1717 4.17605 10.2839 4.25013 10.3585L5.81531 11.9237C5.86645 11.9743 5.93321 11.9999 6 11.9999C6.06679 11.9999 6.13356 11.9743 6.18469 11.9237L7.74988 10.3585C7.82449 10.2839 7.84692 10.1718 7.80622 10.0742C7.76553 9.97662 7.67057 9.91296 7.56519 9.91296H6.78259V6.7826H9.91296V7.56519C9.91296 7.67057 9.97662 7.76605 10.0742 7.80623C10.1707 7.84743 10.2839 7.8245 10.3585 7.74988L11.9237 6.1847C12.0254 6.08245 12.0254 5.91759 11.9237 5.81531Z"
              fill="white"
            />
          </svg>
          <span>1st node</span>
        </>
      )}
    </motion.div>
  );
}
